@import url('../../styles/fonts.scss');
@import url('../../styles/colors.scss');

header.main-header-container {
    width: 100%;
    background-color: var(--header-background-color);
    border: 1px solid var(--header-border-color);
    border-width: 0vw;
    border-bottom-width: 1px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 1vw 0vw;
    position: sticky;
    top: 0;
    z-index: 999;
}

div.header-content-container {
    width: 90%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}

div.header-title p {
    font-family: 'Manrope';
    font-weight: 600;
    font-style: normal;
    font-size: 1.1vw;
    color: var(--header-title-text-color);
}

div.header-title p:hover {
    cursor: pointer;
}

header.main-header-container nav.header-nav-container {
    ul {
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        align-items: center;
    }

    ul li {
        margin-left: 1.2vw;
        font-family: 'Battambang';
        font-weight: 400;
        font-size: 1vw;
        color: var(--header-nav-option-text-color);
        position: relative;
    }

    ul li:hover {
        cursor: pointer;
    }

    ul li::after {
        content: '';
        position: absolute;
        width: 0vw;
        height: 1px;
        background-color: var(--header-nav-option-text-color);
        bottom: 0;
        left: 50%;
        transition: width 0.4s ease, left 0.4s ease;
    }

    ul li:hover::after {
        width: 100%;
        left: 0;
    }
}

header.main-header-container {
    .header-content-container {
        .hamburger-container {
            display: none;
        }

    }

    .menu {
        display: none;
    }
}

@media only screen and (max-width: 480px) {
    header.main-header-container {
        padding: 0 0;

        .header-content-container {
            padding: 3vw 0vw;

            .header-title {
                p {
                    font-size: 5vw;
                }
            }

            .hamburger-container {
                display: block;

                img {
                    width: 9vw;
                    height: auto;
                }
            }

            .header-nav-container {
                display: none;
            }

        }

        .menu {
            position: fixed;
            top: -100vh;
            left: 0;
            width: 100%;
            height: 100vh;
            background-color: var(--header-background-color);
            color: #000;
            transition: top 0.3s ease-in-out;
            padding: 6vw 4vw;
            box-sizing: border-box;
            display: block;

            .header-nav-container {
                ul {
                    flex-direction: column;
                    justify-content: center;
                    align-items: flex-start;

                    li {
                        font-size: 6vw;
                        margin-bottom: 2vw;
                    }
                }
            }
        }
    }
}